@import url("../assets/font/BeVietNam/stylesheet.css");
@import url("../js/library/aos/aos.css");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
* ::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 5px;
  -webkit-appearance: none;
  -webkit-appearance: scrollbar;
  -webkit-overflow-scrolling: touch;
}
* ::-webkit-scrollbar-track {
  height: 4px !important;
  background: #fff;
}
* ::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 16px;
}

html {
  font-size: 62.5%;
}

body {
  color: #262626;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 300;
  font-family: "Be VietNam Pro", sans-serif;
}

select {
  outline: none;
  border: 0;
  background: none;
}

a {
  color: #000;
  text-decoration: none;
}
a:-webkit-any-link:focus-visible {
  outline: 0;
}

img {
  width: 100%;
  display: block;
  max-width: 100%;
  object-fit: cover;
}

.container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
  position: relative;
  max-width: 120rem;
}

.onscroll {
  touch-action: none;
  overflow: hidden;
  overscroll-behavior: none;
  -webkit-overflow-scrolling: none;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu {
  display: block;
}

ol,
ul {
  list-style: none !important;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.btn {
  z-index: 3;
  display: block;
  overflow: hidden;
  line-height: 1.15;
  position: relative;
  background: white;
  border-radius: 0.5rem;
  padding: 0.9rem 3.1rem;
  transition: 0.4s ease-in-out;
  border: 0.1rem solid #002485;
}
@media screen and (min-width: 1201px) {
  .btn:hover {
    color: white;
  }
  .btn:hover::after {
    left: 0;
    right: auto;
    width: 100%;
  }
}
.btn::after {
  content: "";
  top: 0;
  right: 0;
  width: 0;
  z-index: -1;
  height: 100%;
  position: absolute;
  transition: 0.4s ease-in-out;
  background: linear-gradient(216.73deg, #0262e9 50%, #0054ae 94.58%);
}

.tranlate-y {
  transition: 0.4s ease-in-out;
}
@media screen and (min-width: 1201px) {
  .tranlate-y:hover {
    transform: translateY(-1.2rem);
  }
}

@keyframes ring {
  0% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  10% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  20% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  30% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  40% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  50% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}
.header {
  top: 0;
  left: 0;
  width: 100%;
  height: 7rem;
  z-index: 10;
  position: sticky;
  background: white;
  box-shadow: 0 0.2rem 0.9rem rgba(0, 0, 0, 0.14221);
}
.header-wrap {
  height: 100%;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
  justify-content: space-between;
  -webkit-justify-content: space-between;
}
.header-logo {
  width: 9.8rem;
  height: 3.6rem;
}
.header-menu {
  gap: 3rem;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
.header-navigation .menu {
  gap: 3rem;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
.header-navigation .menu-item {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}
@media screen and (min-width: 1201px) {
  .header-navigation .menu-item:hover > a {
    color: #002485;
  }
  .header-navigation .menu-item:hover .ic-down {
    transform: rotate(180deg);
  }
  .header-navigation .menu-item:hover .ic-down::before {
    color: #002485;
  }
}
.header-navigation .menu-item a {
  color: #544837;
  transition: all 0.4s;
}
.header-navigation .menu-item .ic-down {
  transition: all 0.4s;
}
.header-navigation .menu-item .ic-down::before {
  color: #544837;
  font-size: 0.6rem;
}
.header-navigation .menu .dropdown {
  gap: 0.4rem;
}
.header-block {
  gap: 3rem;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
.header-block > div {
  cursor: pointer;
}
.header-block > div:not(:last-child) > div {
  display: block;
  padding: 0.8rem;
  margin: -0.8rem;
  position: relative;
  border-radius: 50%;
  transition: 0.4s ease-in-out;
  width: calc(100% + 1.6rem);
}
@media screen and (min-width: 1201px) {
  .header-block > div:not(:last-child) > div:hover {
    background: rgba(0, 0, 0, 0.05);
  }
  .header-block > div:not(:last-child) > div:hover.phone {
    animation: ring 1.5s infinite ease-in-out;
  }
}
.header-block .ic {
  display: block;
}
.header-block .ic::before {
  font-size: 1.8rem;
  color: #90a8e7;
}
.header-block .language img {
  width: 2.3rem;
  height: 1.5rem;
}